* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} */

.nav--container {
  color: white;
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;

  ul {
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 12px;
    list-style-type: none;
    margin-right: 5px;
  }

  button {
    font-size: 12px;
    font-weight: bold;
    color: white;
    margin-left: 5px;
    background-color: rgba(0, 0, 0, 0);
    border-style: none;
  }

  button:hover {
    cursor: pointer;
    color: yellow;
  }
  button:focus {
    color: white;
  }

  a {
    color: white;
    text-decoration: none;
  }
}

.main--container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  h2 {
    padding-top: 50px;
    font-size: 30px;
  }

  @keyframes bounce {
    0%,
    20%,
    50%,
    80%,
    100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-10px);
    }
    60% {
      transform: translateY(-5px);
    }
  }

  .arrow-up {
    animation-duration: 2s;
    animation-name: bounce;
    margin-top: 60px;
    margin-left: 30px;
    align-self: flex-start;
  }

  .arrow-text {
    font-size: 14px;
    padding: 10px 0px 10px 10px;
    border-style: dashed;
    border-radius: 20px;
    margin-top: 5px;
    margin-left: 5px;
    align-self: flex-start;
    width: 100px;
  }

  .placeHolderArea {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 500px;
    width: 90%;
    border-style: solid;
    border-width: 2px;
    margin-bottom: 100px;
  }
}

.allProjects {
  height: 350px;
  display: flex;
  flex-wrap: no-wrap;
  overflow: auto;
  margin-bottom: 75px;
}

.project--container {
  min-width: 90%;
  margin: 20px 20px 20px 20px;
  display: flex;
  flex-direction: column;
  justify-content: end;
  width: 90%;
  border-radius: 7px;
  background-color: rgba(169, 169, 169, 0.109);
  box-shadow: 0px 0px 15px grey;

  h3 {
    font-size: 22px;
    text-align: center;
    margin-bottom: 10px;
  }

  p {
    color: black;
    color: rgb(71, 69, 69);
    margin: 10px 5px 15px 20px;
  }

  .link-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    a {
      color: rgb(85, 119, 165);
      font-size: 18px;
      text-align: center;
      margin: 7px 10px 10px 10px;
    }
  }
}

.footer--container {
  width: 100%;
  position: relative;
  bottom: 0;
  color: white;
  background-color: rgb(34, 33, 33);
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .friendly-ask-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    p {
      font-size: 16px;
      margin-left: 10px;
      margin-bottom: 10px;
    }
  }

  ul {
    margin-right: 20px;
    padding-left: 0;
    list-style-type: none;
  }

  li {
    margin-top: 15px;
  }

  a {
    color: white;
    text-decoration: none;
    margin-left: 10px;
  }
}
